  objectArray: any;
  viewId: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  pagination: any;
  isGenerate: boolean = false;
  currentPage: number = 1;
  private subscription: ISubscription;

  constructor(
    private dataService: DataService,
    private paginationService: PaginationService,
    private unloadService: UnloadService,
    private location: Location,
    private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.parent.params.subscribe((response) => {
      this.viewId = response['id'];
    });
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  ngOnInit() {
    // this.setHeaderData();
    this.getOrderProductList();
  }

