import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';

import { ISubscription } from "rxjs/Subscription";
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { ProductRegistrationService } from 'src/app/service/product-registration.service';

@Component({
  selector: 'app-order-sorting',
  templateUrl: './order-sorting.component.html'
})
export class OrderSortingComponent implements OnInit {

  objectArray: any;
  viewId: any;
  orderProductListArray: Array<any> = [];
  // headerData: Array<any> = [];
  loadingState = false;
  pagination: any;
  isSorting: boolean = false;
  currentPage: number = 1;
  private subscription: ISubscription;

  constructor(
    private dataService: DataService,
    private paginationService: PaginationService,
    private ProductRegistrationService: ProductRegistrationService,
    private location: Location,
    private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.parent.params.subscribe((response) => {
      this.viewId = response['id'];
    });
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  ngOnInit() {
    // this.setHeaderData();
    this.getOrderProductList();
  }

  back() {
    this.location.back();
  }

  // setHeaderData() {
  //   this.headerData.push(

  //     new TextField({
  //       label: "SKU no",
  //       key: 'product.sku_no',
  //     }),
  //     new TextField({
  //       label: "Product Name",
  //       key: 'product.label',
  //     }),
  //     new TextField({
  //       label: "Receive Qty",
  //       key: 'unload_detail.received_qty',
  //     }),
  //     new TextField({
  //       label: "Pending Qty",
  //       key: 'pending_qty',
  //     }),


  //   );
  //   let canEdit = true;
  //   let actions = {};
  //   //
  //   // View Details
  //   if (canEdit) {
  //     actions['view'] = {
  //       path: '/inbound/registeration/edit/',
  //       id: 'product_register_id',
  //       showText: 'Register Product',
  //       // class: 'fa-bell'
  //     }
  //   }

  //   let actionColom = new ActionField(actions);
  //   this.headerData.push(
  //     actionColom
  //   );
  // }

  // onReloadEvent() {
  //   this.getOrderProductList();
  // }

  getOrderProductList() {
    this.ProductRegistrationService.getSortingList(this.viewId).subscribe((response) => {
      if (response.success && response.data) {
        this.orderProductListArray = response.data.list ? response.data.list : [];
        // this.setOrderId();
        this.isSorting = response.data.is_sorting;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.isSorting = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.isSorting = false;
      this.loadingState = false;
      this.pagination = null;
    });
  }
  // setOrderId() {
  //   this.orderProductListArray.filter((obj) => {
  //     obj.order_id = this.viewId;
  //   });
  // }
  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
