import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { OrderViewComponent } from './order-view.component';
import { OrderDetailsComponent } from './order-details/order-details.component';
import { OrderUnlodingComponent } from './order-unloding/order-unloding.component';
import { OrderSortingComponent } from './order-sorting/order-sorting.component';
import { OrderPutawayComponent } from './order-putaway/order-putaway.component';
import { OrderCompletedComponent } from './order-completed/order-completed.component';


const routes: Routes = [
  {
    path: '',
    component: OrderViewComponent,
    children: [
      {
        path: '',
        component: OrderDetailsComponent,
        data: { title: 'view_order' }
      },
      {
        path: 'unloading',
        component: OrderUnlodingComponent,
        data: { title: 'view_order' }
      },
      {
        path: 'sorting',
        component: OrderSortingComponent,
        data: { title: 'view_order' }
      },
      {
        path: 'putaway',
        component: OrderPutawayComponent,
        data: { title: 'view_order' }
      },
      {
        path: 'completed',
        component: OrderCompletedComponent,
        data: { title: 'view_order' }
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrderViewRoutingModule { }
