import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UnloadComponent } from './unload.component';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableViewModule } from 'src/app/common-module/table-view/table-view.module';
import { Routes, RouterModule } from '@angular/router';
import { Ng2FlatpickrModule } from 'ng2-flatpickr';
import { UnloadService } from 'src/app/service/unload.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';


const routes: Routes = [
  {
    path: '',
    component: UnloadComponent,
    data: { title: 'unload_list' },
  },
  {
    path: 'generate',
    loadChildren: () => import('./generate/generate.module').then(m => m.GenerateModule),
    data: { title: 'add_unload' },
  },
  {
    path: 'view',
    loadChildren: () => import('./unloading-view/unloading-view.module').then(m => m.UnloadingViewModule),
    data: { title: 'view_unload' },
  },
  {
    path: 'generate-unloading',
    loadChildren: () => import('./generate-unloading/generate-unloading.module').then(m => m.GenerateUnloadingModule),
    data: { title: 'add_unload' },
  },

]

@NgModule({
  declarations: [UnloadComponent],
  imports: [
    CommonModule,
    SearchModule,
    BtnAddModule,
    PaginationModule,
    TableListModule,
    RouterModule.forChild(routes)
  ], providers: [UnloadService]
})
export class UnloadModule { }
