import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: 'order',
    loadChildren: () => import('./order/order.module').then(m => m.OrderModule),
    data: { title: 'order_list' },
  },
  {
    path: 'unload',
    loadChildren: () => import('./unload/unload.module').then(m => m.UnloadModule)
  },
  {
    path: 'registeration',
    loadChildren: () => import('./registeration/registeration.module').then(m => m.RegisterationModule)
  },
  {
    path: 'product-config',
    loadChildren: () => import('./product-config/product-config-list/product-config-list.module').then(m => m.ProductConfigListModule)
  },
  {
    path: 'put-away',
    loadChildren: () => import('./put-away/put-away.module').then(m => m.PutAwayModule)
  }
  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InboundRoutingModule { }
