
import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { ISubscription } from "rxjs/Subscription";
import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { PutAwayService } from 'src/app/service/put-away.service';
import { errorMessage } from 'src/app/messages';
declare var swal:any;
@Component({
  selector: 'app-order-putaway',
  templateUrl: './order-putaway.component.html'
})
export class OrderPutawayComponent implements OnInit {

  objectArray: any;
  viewId: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  pagination: any;
  isGenerate: boolean = false;
  currentPage: number = 1;
  private subscription: ISubscription;
  statusArray: any=[];

  constructor(
    private dataService: DataService,
    private paginationService: PaginationService,
    private PutAwayService: PutAwayService,
    private location: Location,
    private activatedRoute: ActivatedRoute
  ) {

    this.activatedRoute.parent.params.subscribe((response) => {
      this.viewId = response['id'];
    });
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  ngOnInit() {
    this.getOrderProductList();
  }

  back() {
    this.location.back();
  }

  getOrderProductList() {
    this.PutAwayService.getTransferOrderListById(this.viewId).subscribe((response) => {
      if (response.success && response.data.list.length > 0) {
        this.orderProductListArray = response.data.list ? response.data.list : [];
        this.statusArray=response.data.status;
        if (this.orderProductListArray.length > 0) {
          this.setOrderId();
        }
        this.isGenerate = response.data.is_generate;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.isGenerate = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.isGenerate = false;
      this.loadingState = false;
      this.pagination = null;
    });
  }


  setOrderId() {
    this.orderProductListArray.filter((obj) => {
      obj.order_id = this.viewId;
    });
  }

  statusChange(transfer_order_id, status_id, current_status_id) {
    if (current_status_id != status_id) {
      swal.fire({
        title: errorMessage.status_change_header_text,
        text: errorMessage.status_change_smalll_text,
        icon: errorMessage.status_change_dialogue_type,
        showCancelButton: true,
        confirmButtonText: errorMessage.status_change_confirm_button,
        cancelButtonText: errorMessage.status_change_cancel_button,
      }).then((result) => {
        if (result.value) {
          if (transfer_order_id && status_id) {
            let data = { status_id: status_id }
            this.PutAwayService.statusChange(transfer_order_id, data).subscribe(
              (response) => {
                if (response.success) {
                  this.getOrderProductList();
                }
              },
            );
          }
        }
      }).catch(swal.noop);
    }
  }
  
  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}


