import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class ProductRegistrationService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getProductRegistrationList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting`, { params: param });
  }

  addProductRegistration(data): Observable<any> {
    return this.http.post(`${this.API_URL}/sorting`, data);
  }

  editProductRegistration(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/sorting/${id}`, data);
  }

  getProductRegistrationById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/${id}`, { params: param });
  }


  getMasterData(param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/drop-down`, { params: param });
  }

  getTagData(param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/get-tags`, { params: param });
  }

  
  getSortingList(id): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/get-list/${id}`);
  }

  getSortedListById(product_id,order_id,param): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/get-sorted/${product_id}/${order_id}`, { params: param });
  }

  // ==> product-config

  // addProductConfiguration(data): Observable<any> {
  //   return this.http.post(`${this.API_URL}/sorting/product-config`, data);
  // }

  // editProductConfiguration(id, data): Observable<any> {
  //   return this.http.put(`${this.API_URL}/sorting/product-config/${id}`, data);
  // }

  getProductConfigurationById(product_id,order_id): Observable<any> {
    return this.http.get(`${this.API_URL}/sorting/get-config/${product_id}/${order_id}`);
  }

}

