import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OrderService } from 'src/app/service/order.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { DataService } from 'src/app/service/data.service';
import { ISubscription } from "rxjs/Subscription";
import { TextField, PriceField, IndexField } from 'src/app/common-module/table-list/list-field-type';

@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html'
})
export class OrderDetailsComponent implements OnInit {
  viewId: string;
  objectArray: any;
  pagination: any;
  orderProductListArray: Array<any> = [];
  headerData: Array<any> = [];
  loadingState = false;
  currentPage: number = 1;
  private subscription: ISubscription;
  timezone: any;
  time_format: any;
  date_format:string="M/d/yy";
  currentCompany: any;

  constructor(private activatedRoute: ActivatedRoute,
    private orderService: OrderService,
    private dataService: DataService,
    private paginationService: PaginationService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
    this.getOrderDetails();
  }

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
    });
   
    this.setHeaderData();
    this.getOrderProductList();
  }

  setHeaderData() {
    this.headerData.push(
      new IndexField({
        label: "No.",

      }),
      new TextField({
        label: "SKU No #",
        key: 'sku_no',
      }),
      new TextField({
        label: "Product",
        key: "product_name",
      }),
       new TextField({
        label: "Order Quantity",
        key: "qty",
      }),
      new PriceField({
        label: "Price",
        key: "price",
      }),
      new PriceField({
        label: "Total Price",
        key: "amount",
      }),
      
    );

  }


  getOrderDetails() {
    this.subscription = this.dataService.OrderDetails.subscribe((data) => {
      if (data) {
        this.objectArray = data;
      }
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.currentPage = 1;
    this.getOrderProductList();
  }

  getOrderProductList() {
    const params: any = { order_id: this.viewId };
    this.orderService.getOrderProductList(params).subscribe((response) => {
      if (response.success && response.data) {
        this.orderProductListArray = response.data.list;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderProductListArray = [];
        this.loadingState = false;
        this.pagination = null;
      }
    }, (error) => {
      this.orderProductListArray = [];
      this.loadingState = false;
      this.pagination = null;

    });
  }


  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}
