import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./order-list/order-list.module').then(m => m.OrderListModule),
    data: { title: 'order_list' },
  },
  {
    path: 'add',
    loadChildren: () => import('./add-order/add-order.module').then(m => m.AddOrderModule),
    data: { title: 'add_order' },
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('./add-order/add-order.module').then(m => m.AddOrderModule),
    data: { title: 'edit_order' },
  },
  {
    path: 'view/:id',
    loadChildren: () => import('./order-view/order-view.module').then(m => m.OrderViewModule),
    data: { title: 'view_order' },
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrderRoutingModule { }
